import Vue, { PluginFunction, PluginObject } from 'vue';
import moment from 'moment';

declare module 'vue/types/vue' {
  interface Vue {
    $dateTime: DateTimeFormmat;
  }
}

export class DateTimeFormmat extends Vue {
  /**
  * 
  * @param {string} adDate  e.g.2021/12/13
  * @returns {string}  e.g. 110/12/13
  */
  public TWDateFormatter(adDate) {
    if (adDate) {
      const arr = adDate.split('/');
      const [yyyy, mm, dd] = arr;
      const twYear = parseInt(yyyy) - 1911;
      return `${twYear}/${mm}/${dd}`;
    }
    return '';
  }

  /**
  * ISO8601
  * @param {string} str  e.g.2021-12-11T15:49:12.000+0800
  * @returns {string}  e.g. 2021/12/11
  */
   public ADDateFormatter(str) {
    if (str) {
      return moment(new Date(str)).format('YYYY/MM/DD');
    }
    return '';
  }

  /**
  * ISO8601
  * @param {Date} date  e.g.Sat Dec 11 2021 15:49:12 GMT+0800 ()
  * @returns {string} ISO8601 e.g. 2021-12-11T15:49:12.000+0800
  */
  public ISO8601DateFormatter(date) {
    if (date) {
      return moment(date).format('YYYY-MM-DDTHH:mm:ss.SSSZZ');
    }
    return '';
  }

  /**
  * ISO8601 ()
  * @param {string} date ISO8601 e.g.2021-12-11T15:49:12.000+0800
  * @param {string} formatter e.g.YYYY/MM/DD HH:mm
  * @returns {string}  e.g. 110/11/17 13:19
  */
  // ISO860   ()  e.g. 110/11/17 13:19
  //   this.$dateTime.customTWDateFormatter(<>, 'YYYY/MM/DD HH:mm')
  // 
  public customTWDateFormatter(date, formatter) {
    if (date) {
      const twYear = moment(date).year() - 1911; // 
      const lessYear = formatter.replace(/^(Y*\/)/, '');
      return `${twYear}/${moment(date).format(lessYear)}`;
    }
    return '';
  }

  public install(Vue) {
    Vue.prototype.$dateTime = this;
  }
}

export default new DateTimeFormmat();
