    return this.$global.getPolicyType(this.policyNum);
    // return 'CB';
  }

  searchUnitCo: PolicyMaintainDto = {};

  rules: { [key: string]: ValidationRule[] } = {
    // 
    addAndAbandonDevelopmentDays: [
      {
        required: true,
        message: '',
      },
      {
        pattern: /^[0-9]{1,}$/,
        message: '',
      },
    ],
    // 
    effectiveWay: [
      {
        required: true,
        message: '',
